html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 25px;
  color: #111111;
  list-style-type: none;
  background-color: white;
}

p {
  margin: 0;
  padding: 0;
  font-family: "Arial", "Helvetica CY", "Nimbus Sans L", sans-serif;
  font-weight: normal;
  font-size: 14px;
}

h1 {
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Helvetica CY", "Arial", sans-serif;
  box-sizing: border-box;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip-patch: inset(100%);
  clip: rect(0 0 0 0);
}

img {
  max-width: 100%;
}

iframe {
  border: none;
}

input[type="submit"] {
  appearance: none;
  border-radius: 0;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  appearance: none;
  border-radius: 0;
}
