@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswaldregular.woff") format("woff"), url("../fonts/Oswaldregular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswaldmedium.woff") format("woff"), url("../fonts/Oswaldmedium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 25px;
  color: #111111;
  list-style-type: none;
  background-color: white;
}
p {
  margin: 0;
  padding: 0;
  font-family: "Arial", "Helvetica CY", "Nimbus Sans L", sans-serif;
  font-weight: normal;
  font-size: 14px;
}
h1 {
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Helvetica CY", "Arial", sans-serif;
  box-sizing: border-box;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip-patch: inset(100%);
  clip: rect(0 0 0 0);
}
img {
  max-width: 100%;
}
iframe {
  border: none;
}
input[type="submit"] {
  appearance: none;
  border-radius: 0;
}
input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  appearance: none;
  border-radius: 0;
}
.page-header {
  display: flex;
  flex-direction: column;
  height: auto;
  background-color: #ffffff;
}
.page-header__logo-image-tablet {
  display: none;
}
.page-header__logo-image-desktop {
  display: none;
}
.page-header__logo-toogle-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-bottom: 1px solid #e6e6e6;
}
.page-header__logo-wrapper {
  width: 65px;
  height: 65px;
  padding-left: 8px;
}
.page-header__logo-wrapper:hover {
  opacity: 80%;
}
.page-header__logo-wrapper:active {
  opacity: 60%;
}
.page-header__logo-text-wrapper {
  display: flex;
