  justify-content: center;
  width: 33px;
  height: 60px;
  margin: 0 auto;
  padding: 0;
}
.page-header__logo-text {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 101px;
  height: 20px;
  margin: 0 auto;
  padding: 0;
}
.page-header__toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 65px;
  border-style: none;
  cursor: pointer;
  margin: 0 auto;
  padding: 0;
  background-color: transparent;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.main-nav--closed {
  display: none;
}
.main-nav--opened {
  display: block;
}
.page-header__toggle--active {
  display: block;
}
.page-header__toggle--none {
  display: none;
}
.page-header__cross--active {
  display: block;
}
.page-header__cross--none {
  display: none;
}
.page-header__cross {
  border: none;
  width: 65px;
  height: 65px;
  cursor: pointer;
  background-color: transparent;
  margin: 0;
  padding: 0;
}
.site-list__item {
  height: 65px;
  text-align: center;
  border-bottom: 1px solid #e6e6e6;
}
.site-list__item:hover {
